	module.exports = {"data":{"markdownRemark":{"html":"<h3>Hacker Email Alias</h3>\n<p>All hackers now have an <a href=\"/hackers/hacker-email-alias.html\">email alias</a> that forwards emails to the email address they’ve registered with on HackerOne. This provides an easy way for programs to contact you in order to share credentials and information without having to access your actual email address.</p>\n<h3>Sort Notifications</h3>\n<p>You can now sort notifications from oldest to newest and vice versa.\n!\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2018_notifications_sort-fef58f26a88b30acccc408a447bdce9e-47697.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 379px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100.52770448548813%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"july_2018_sort_notifications\"\n        title=\"\"\n        src=\"/static/july_2018_notifications_sort-fef58f26a88b30acccc408a447bdce9e-47697.jpg\"\n        srcset=\"/static/july_2018_notifications_sort-fef58f26a88b30acccc408a447bdce9e-d2bbe.jpg 125w,\n/static/july_2018_notifications_sort-fef58f26a88b30acccc408a447bdce9e-d4b7f.jpg 250w,\n/static/july_2018_notifications_sort-fef58f26a88b30acccc408a447bdce9e-47697.jpg 379w\"\n        sizes=\"(max-width: 379px) 100vw, 379px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Hacktivity Redesign</h3>\n<p>We've revamped the look of our <a href=\"https://hackerone.com/hacktivity\">Hacktivity</a> feed so that it has a sleeker design. We've also deprecated the <b>Top</b> tab on Hacktivity.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-f608b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 38.74439461883408%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"july_2018_hacktivity\"\n        title=\"\"\n        src=\"/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-e7ffa.png\"\n        srcset=\"/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-49813.png 125w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-dc8a5.png 250w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-e7ffa.png 500w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-dda24.png 750w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-c5f18.png 1000w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-22484.png 1500w,\n/static/july_2018_hacktivity-1f0258eb79f8169c7e48fc6a760384fb-f608b.png 2230w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Bug Fixes</h3>\n<ul>\n<li>Hackers that have submitted a report and left the program can now revisit the report without seeing any errors.</li>\n<li>The reworded the notification for invites to private programs so that it's clear that it's an invitation.</li>\n</ul>","frontmatter":{"path":"/changelog/2018/july","title":"July 2018"}}},"pathContext":{}}

